import fromMiddle from './../utils/fromMiddle'

const mover = (element, e) => {
  // Do the moving
  const a = element.parentElement, // Parent === area
    offset = fromMiddle(a, e),
    areaBorderX = offset.width / 2,
    areaBorderY = offset.height / 2,
    maxX = offset.x > areaBorderX // Max move is area border
    ?
    areaBorderX // To the right
    :
    offset.x < -areaBorderX ?
    -areaBorderX // And left
    :
    offset.x, // Else move freely
    maxY = offset.y > areaBorderY ?
    areaBorderY :
    offset.y < -areaBorderY ? -areaBorderY : offset.y

  Object.assign(a.style, {
    position: "relative",
    zIndex: "1000"
  })

  Object.assign(element.style, {
    position: "relative",
    zIndex: "1001",
    transform: `translate(
            ${maxX / 2}px,
            ${maxY / 5}px) scale(0.5, 0.2)`,
    transition: 'all .7s cubic-bezier(0.4, 1.4, 1, 1)',
    background: 'red'
  })

  a.addEventListener(
    "mouseleave",
    resetTransform.bind(null, a, element, true),
    false
  ) // Reset on mouse leave
  return element
}

const addMover = element =>
  element.parentElement.addEventListener(
    "mousemove",
    mover.bind(null, element),
    false
  );

export default elements.magnet
  .map(element =>
    addMover(
      addArea(element)
    )
  )
