import elements from './elementsSorted'
import magnet from './components/magnet'


const flyImgSrc =
"data:image/png;base64,"+
""+
"";

// Utility
const inView = element => {
  // Returns true if element is is in view
  const rect = element.getBoundingClientRect();

  return (
    (rect.top >= 0 &&
      rect.top <=
      (window.innerHeight || document.documentElement.clientHeight)) ||
    (rect.bottom >= 0 &&
      rect.bottom <=
      (window.innerHeight || document.documentElement.clientHeight))
  );
};

const addArea = element => {
  // Add a span that becomes the area of effect
  const sizes = element.getBoundingClientRect(),
    area = document.createElement("span");

  area.classList.add("yoyo-area");

  Object.assign(area.style, {
    width: `${sizes.width}px`,
    height: `${sizes.height}px`,
    transform: 'scale(2, 5)',
    display: "flex",
    justifyContent: "center",
    alignItems: "center"
  });

  Object.assign(element.style, {
    transform: 'scale(0.5, 0.2)',
  })

  element.parentNode.insertBefore(area, element);
  area.appendChild(element);

  return element;
};

const addImage = (element, image) => {
  const img = document.createElement("IMG");

  img.setAttribute("src", image);
  Object.assign(img.style, {
    position: "absolute",
    transform: "rotate(120deg)",
    opacity: "0.9",
    top: "0",
    left: "0"
  });
  element.appendChild(img)

  return img;
};

const putInCorner = (element, margin) => {
  Object.assign(element.parentElement.style, {
    position: "absolute",
    top: `${margin}px`,
    right: `${margin}px`
  });
  return element
};

const addFader = element =>
  element.parentElement.addEventListener(
    "mousemove",
    fader.bind(null, element),
    false
  );

const addAvoid = element =>
  element.parentElement.addEventListener(
    "mousemove",
    avoider.bind(null, element),
    false
  );

const addTextSwap = element =>
  element.parentElement.addEventListener(
    "mousemove",
    textSwap.bind(null, element),
    false
  );

const resetTransform = (area, element, andScale) => {
  area.style.zIndex = 0;
  if (andScale) {
    return Object.assign(element.style, {
      transform: 'scale(0.5, 0.2)'
    })
  } else {
    return element.style.transform = "translate(0,0)"
  }
};

const resetOpacity = (area, element, to) => {
  // Set opacity to (to)
  area.style.zIndex = 0;
  return (element.style.opacity = `${to}`);
};

const furthestOfXY = offset => {
  // Returns % cursor is from middle (how long the radius is)
  const pixels = Math.max(Math.abs(offset.x), Math.abs(offset.y)),
    percent = pixels / (offset.width / 100) * 2;

  return Math.round(percent);
};

const horiVertiPosition = offset => { // Not in use atm
  // Returns % cursor is from middle X and Y
  const percentX = offset.x / (offset.width / 100) * 2,
    percentY = offset.y / (offset.height / 100) * 2

  return {
    xNeg: Math.round(percentX),
    xAbs: Math.abs(Math.round(percentX)),
    yNeg: Math.round(percentY),
    yAbs: Math.abs(Math.round(percentY))
  }
}

const fader = (element, e) => {
  const a = element.parentElement, // Parent === area
    offset = fromMiddle(a, e),
    areaBorderX = offset.width / 2,
    areaBorderY = offset.height / 2,
    percentFromMiddle = furthestOfXY(offset)

  Object.assign(element.style, {
    opacity: percentFromMiddle / 100
  });

  a.addEventListener(
    "mouseleave",
    resetOpacity.bind(null, a, element, 1),
    false
  ); // Reset on mouse leave
  return element
};

const grow = arrOfElements => {
  arrOfElements.map(element => {
    if (inView(element) && !element.classList.contains("growing")) {
      element.classList.add("growing");
      Object.assign(element.style, {
        position: "relative",
        zIndex: "1001",
        transform: "scale(10)",
        transition: "360s linear"
      });
    } else if (!inView(element) && element.classList.contains("growing")) {
      element.classList.remove("growing");
      Object.assign(element.style, {
        zIndex: "1001",
        transform: "scale(1)",
        transition: "1s linear"
      });
    }
  });
  return arrOfElements
};

const avoider = (element, e) => {
  const a = element.parentElement, // Parent === area
    popUp = element.parentNode.parentNode,
    offset = fromMiddle(a, e),
    areaBorderX = offset.width / 2,
    areaBorderY = offset.height / 2,
    maxX = offset.x > 0 // If cursor is inside to the left
    ?
    areaBorderX + 20 // Move to the right
    :
    -areaBorderX - 20, // Else to the left
    maxY = offset.y > 0 ?
    areaBorderY + 20 :
    -areaBorderY - 20;

  Object.assign(popUp.style, {
    zIndex: "1001",
    transform: `translate(
            ${-maxX}px,
            ${-maxY / 2.5}px)`,
    transition: 'all 2.8s cubic-bezier(0.4, 1.4, 1, 1)'
  });
  return element
};

const moveFlies = arrOfElements => {
  arrOfElements.map(element => {
    const rect = fromMiddle(element.parentElement, 0),
      random = {
        x: Math.floor(Math.random() * rect.width),
        y: Math.floor(Math.random() * rect.height),
        rotation: Math.floor(Math.random() * 360)
      };

    if (inView(element) && !element.classList.contains("moving")) {
      element.classList.add("moving");
      Object.assign(element.style, {
        transform: `translate(
              ${random.x}px,
              ${random.y}px)
              rotate(${random.rotation}deg)`,
        transition: "transform 60s ease-out"
      });
    } else if (!inView(element) && element.classList.contains('moving')) {
      element.classList.remove("moving");
      Object.assign(element.style, {
        transform: `translate(
              ${random.x}px,
              ${random.y}px)
              rotate(${random.rotation}deg)`,
        transition: "transform 1s ease-out"
      });
    }
  });
  return arrOfElements
};

const addSwapText = element => {
  const swapTo = element.dataset.swap, // What's in the data-attr
    swapToContainer = document.createElement('SPAN'), // To put new text in
    swapToAsNode = document.createTextNode(swapTo), // The new text
    originalText = element.firstChild, // The text in the button
    originalTextContainer = document.createElement('SPAN'), // To wrap original text
    originalTextAsNode = document.createTextNode(originalText.nodeValue)

  originalTextContainer.appendChild(originalTextAsNode); // Put original text in span
  element.replaceChild(originalTextContainer, originalText); // Replace original text with span wraped text
  swapToContainer.appendChild(swapToAsNode) // Append new text as well
  element.appendChild(swapToContainer) // And put into button

  Object.assign(swapToContainer.style, {
    position: 'absolute',
    width: '100%',
    height: '100%',
    left: '0%',
    opacity: '0',
    textAlign: 'center',
    cursorEvents: 'none',
  })

  return element
}

const textSwap = (element, e) => {
  const a = element.parentElement, // Parent === area
    offset = fromMiddle(a, e),
    areaBorderX = offset.width / 2,
    areaBorderY = offset.height / 2,
    percentFromMiddle = furthestOfXY(offset),
    opacity = percentFromMiddle / 100

  Object.assign(element.firstChild.style, {
    opacity: opacity,
    transition: 'all 4.7s cubic-bezier(0.075, 0.82, 0.165, 1)'
  })
  Object.assign(element.lastElementChild.style, {
    opacity: 1 - opacity,
    transition: 'all 4.7s cubic-bezier(0.075, 0.82, 0.165, 1)'
  })

  a.addEventListener(
    "mouseleave",
    resetOpacity.bind(null, a, element.firstChild, 1),
    false
  );
  a.addEventListener(
    "mouseleave",
    resetOpacity.bind(null, a, element.lastChild, 0),
    false
  );
  return element
}

// Setup
// TODO: MAKE READABLE..? WRITE A PIPE FUNCTION
const fadingsWithArea = elements.fading
  .map(element => {
    return addFader(
      addArea(element)
    )
  })

const avoidingWithArea = elements.avoiding
  .map(element => {
    return addAvoid(
      putInCorner(
        addArea(element), 10
      )
    )
  })

const elementsWithFly = elements.fly
  .map(element => {
    return addImage(element, flyImgSrc)
  })

const swapsTextsWithArea = elements.swap
  .map(element => {
    return addTextSwap(
      addSwapText(
        addArea(element)
      )
    )
  })

const dispatchScroll = () => {
  grow(elements.grow);
  moveFlies(elementsWithFly);
};
window.addEventListener("scroll", dispatchScroll, true);
